import Breadcrumb from '../components/common/Breadcrumb.vue'
import config from '../config/config'

export default {
    layout: 'social',
    components: { Breadcrumb },
    head: {
        title: 'Thanh toán' + config.siteName,
            htmlAttrs: {
            lang: 'en'
        },
        meta: [
            { charset: 'utf-8' },
            { name: 'viewport', content: 'width=device-width,initial-scale=1.0,user-scalable=0,minimal-ui' },
            { hid: 'description', name: 'description', content: config.meta.payment.description },
            { hid: 'og:description', name: 'og:description', content: config.meta.payment.description },
            { hid: 'keywords', name: 'keywords', content: config.meta.payment.keywords },
            { hid: 'dc.keywords', name: 'dc.keywords', content: config.meta.payment.keywords },
            { hid: 'news_keywords', name: 'news_keywords', content: config.meta.payment.keywords },
            { hid: 'og:site_name', name: 'og:site_name', content: config.siteName2 },
            { hid: 'dc.title', name: 'dc.title', content: config.title },
            { hid: 'og:title', name: 'og:title', content: config.title },
            { name: 'format-detection', content: 'telephone=no' },
        ],
    },
    data() {
        return {
            payments: [],
            user: this.$store.state.user,
            breadcrumbs: {
                title: 'Nạp tiền',
                items: [
                    'Trang chủ',
                    'Nạp tiền',
                ],
                links: [
                    '/home',
                    '/topup',
                ]
            },
        }
    },
    methods: {
        async getPayment() {
            try {
                let response = await this.$http.get('/v2/get-payment')
                this.payments = response.data.data
            } catch (error) {
                this.$notify.error({
                    title: 'Error',
                    message: error.response.data.message
                });
            }
        },
        async copy (string) {
            let self = this;
            navigator.clipboard.writeText(string).then(function () {
                self.$notify.success({
                    title: 'success',
                    message: "Copy thành công " + string
                });
                
            }, function (err) {
                self.$notify.error({
                    title: 'Error',
                    message: err
                });
            });
        }
    },
    watch: {
        '$store.state.user': function() {
            this.user = this.$store.state.user
        }
    },
    mounted() {
        this.getPayment();
        feather.replace({
            width: 14,
            height: 14
        });
    }
}
